/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { push } from 'react-router-redux';

import './LandingPage.css';
import NavBar from './components/NavBar';
import { store } from '../../store';
import Footer from './components/Footer';
import MobileDevice from './components/MobileDevice';
import Pricing from './components/Pricing';
import phone from '../../assets/Images/LandingPage/feature-img.jpeg';
// import shop_banner from '../../assets/Images/LandingPage/shopping-banner.png'
import shop_banner_2 from '../../assets/Images/LandingPage/shopping_banner-2.png';
import shop_banner_3 from '../../assets/Images/LandingPage/online-shopping-png.png';
import analytics from '../../assets/Images/LandingPage/analytics-img.svg';
import agent from '../../agent';
import { GET_PLANS } from '../../constants/actionTypes';
import { WEB_TOKEN, APP_NAME } from '../../constants/otherConstants';
import ScrollToTopWrapper from '../../common/Components/ScrollToTopWrapper';

class LandingPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loggedIn: false,
    };
    this.myDivToFocus = React.createRef();
  }

  componentWillMount() {
    const token = window.localStorage.getItem(WEB_TOKEN);
    if (token) {
      this.setState({ loggedIn: true });
    } else {
      this.setState({ loggedIn: false });
    }
  }

  handleOnClick = (event) => {
    // .current is verification that your element has rendered
    if (this.myDivToFocus.current) {
      this.myDivToFocus.current.scrollIntoView({
        behavior: 'smooth',
        block: 'nearest',
      });
    }
  }

  componentWillReceiveProps(np) {
    // console.log(np.plans);

  }

  render() {
    const { loggedIn } = this.state;
    return (
      <>
        <NavBar loggedIn={this.state.loggedIn} />
        
          <div className="gradient-bg-landing-page">
            <div className="mobile_header_sdk_img">
              <svg xmlns="https://www.w3.org/2000/svg" viewBox="0 0 1418 995" id="svg-icon">

                <defs>
                  <clipPath id="clip-path">

                    <rect id="Rectangle_696" data-name="Rectangle 696" className="cls-1" width="1418" height="995" transform="translate(502)" />
                  </clipPath>
                </defs>
                <g id="Mask_Group_36" data-name="Mask Group 36" className="cls-2" transform="translate(-502)">

                  <path id="Path_546" data-name="Path 546" className="cls-3" d="M3690.005,707.591c133.569,324.227,422.867,194.932,474.868,344.832s-34.88,420.957,447.224,625.954,468.65,145.523,468.65,145.523-28.86-370.6,111.43-621.966-116.268-156.175-44.342-331.1S5091.89,707.666,5126.521,653.2s-274.385,42.715-356.968-32.25,50.616,8.33-303.69-13.883-58.607-24.989-338.322-22.212S3556.437,383.365,3690.005,707.591Z" transform="translate(-3235.185 -479.435) rotate(-4)" />

                </g>
              </svg>
            </div>
            {/* <ScrollToTopWrapper> */}
            <div className="main-background">


              <div className="">
                <div className="mobile-div main-div-width " style={{ height: '100vh' }}>
                  <div className="tc center white home-page-header">
                    <h1>Start your Facebook, Instagram Store in minutes </h1>
                    <p>
                    It's quick and easy.
                    </p>
                    {
                  loggedIn
                    ? (
                      <div className=" offer-div ">
                        <button type="button" className="color-btn w-100 " onClick={() => store.dispatch(push('/admin/products/'))}>
                        Go To My Account
                        </button>
                      </div>
                    )
                    : (
                      <div className="landing-btn-div">
                        <button type="button" className="" onClick={() => store.dispatch(push('/signup'))}>
                        Get Started Free
                        </button>
                        <button type="button" className="" onClick={() => store.dispatch(push('/login'))}>
                        Login
                        </button>
                      </div>
                    )
                }


                  </div>

                  <div>
                    <MobileDevice />
                  </div>
                </div>
                <div className=" landing-page-second-div">
                  <div className="  div-1">
                    <h1>2,000,000,000</h1>
                    <h3>potential customers</h3>
                  </div>
                  <div className="div-2">
                    <h4>
                  Harness the power of Facebook and Instagram with your own Facebook Store and access over 2 billion unique shoppers
                    </h4>
                  </div>

                </div>
                <div className="middle-content" id="products">

                  <div className="landing-page-feature-content">
                    <img src={shop_banner_3} alt="" className="feature-img-normal" />
                    <div className="content">
                      <h1>
                        {`Manage your Facebook and Instagram Store from your ${APP_NAME} dashboard`}
                      </h1>
                      <p>
                        {`Easily add, promote, and sell products, or manage all your Facebook and Instagram advertising in just a few clicks right from your ${APP_NAME} dashboard.`}
                      </p>

                    </div>

                  </div>
                  <div className="landing-page-feature-content">

                    <div className="content">
                      <h1>
                    Sell Online With
                        {APP_NAME}
                      </h1>
                      <h3>
                  Beautiful themes that are responsive and customizable
                      </h3>

                      <p>No design skills needed. You have complete control over the look and feel of your website, from its layout, to content and colors.</p>

                    </div>
                    <img src={shop_banner_2} alt="" className="feature-img-normal" />
                  </div>
                </div>
                <div className=" landing-page-third-div">
                  <h1>Accept orders with ZERO setup fees</h1>
                  <h3>{`To start accepting  customer order through ${APP_NAME}, we will charge ZERO setup fee from you, Seriously.`}</h3>
                </div>
                <div className="middle-content">
                  <div className="landing-page-feature-content">
                    <img src={phone} alt="" className="feature-img" />
                    <div className="content">
                      <h1>Add Multiple Links In your Instagram Bio</h1>
                      <p>
                    Instagram only allows one clickable link, make the most out of it with MyWeblink
                      </p>
                      <div className="ul-list">
                        <ul>
                          <li>Unlimited Links</li>
                          <li>Link Scheduling</li>
                          <li>Social Media Integration</li>

                        </ul>
                        <ul>
                          <li>Analytics</li>
                          <li>Custom Themes</li>
                          <li>Custom Logos</li>
                        </ul>
                      </div>

                    </div>
                  </div>
                  <div className="landing-page-feature-content">
                    <div className="content">
                      <h2>Analytics and Insights</h2>
                      <p>Know how many followers visit your website and discover which which content is performing with your audience.</p>
                      <h2>Customize your Brand Experience</h2>

                      <p>We care for your brand experience.Modify the logo and the brand name as per your own companys assets.</p>
                    </div>


                    <img src={analytics} alt="" />

                  </div>


                </div>

                {/* <div id="pricing">
              <Pricing />
            </div> */}

                <div className="tc signup-content">

                  <h1>
                  Get the power of MyWebLink.store
                  </h1>
                  {
                loggedIn
                  ? <button className="signup" type="button" onClick={() => store.dispatch(push('/signup'))}>Go To My Account</button>
                  : (
                    <div>
                      <p> Sign up today or login </p>

                      <button className="signup" type="button" onClick={() => store.dispatch(push('/signup'))}>Sign up</button>
                      <button className="login" type="button" onClick={() => store.dispatch(push('/login'))}>Login</button>

                    </div>
                  )
              }

                </div>
                <Footer />
              </div>

            </div>
            {/* </ScrollToTopWrapper> */}
          </div>
        
      </>
    );
  }
}


const mapStateToProps = (state) => ({
  plans: state.PurchaseReducers.plans,
});

const mapDispatchToProps = (dispatch) => ({
  getPlans: () => dispatch({ type: GET_PLANS, payload: agent.Purchase.getPlans() }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(LandingPage);
