/* eslint-disable no-nested-ternary */
/* eslint-disable camelcase */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { toast } from 'react-toastify';
import { push } from 'react-router-redux';
import './styles.css';
import _ from 'lodash';
import {
  ADD_TO_CART, REMOVE_FROM_CART, SELECT_PRODUCT,
} from '../../../constants/actionTypes';
// import ProductDetails from './ProductDetails';
import { store } from '../../../store';
import CollapseTransition from '../../../common/Components/CollapseTransition';
import Image from '../../../common/Components/Image';
import checkBgColor from '../../../common/Components/checkBgColor';
import { DEFAULT_TEXT_COLOR } from '../../../constants/otherConstants';


class PublicProduct extends Component {
  constructor(props) {
    super(props);
    this.state = {
      product: { },
      quantity: 1,
      alreadyInCart: false,
      cartItem: false,
      isSizesAvailable: false,
      selectedSize: null,
      sizes: [],
      textColor: DEFAULT_TEXT_COLOR,
      user: {},
      stockAvailable: true,
    };
    this.cartItems = [];
    this.selectRef = React.createRef();
    this.showCart = this.showCart.bind(this);
    this.showProductDetails = this.showProductDetails.bind(this);
  }

  componentWillMount() {
    const {
      data, cartItem, cartItems, homeBgStyle, publicPageBgStyle, parent, publicPageUser,
    } = this.props;
    if (parent && parent === 'admin-page') {
      if (homeBgStyle) {
        let textColor = DEFAULT_TEXT_COLOR;
        if (homeBgStyle.link_style) {
          textColor = homeBgStyle.link_style;
        }
        this.setState({ textColor });
      }
    } else if (publicPageBgStyle) {
      let textColor = DEFAULT_TEXT_COLOR;
      if (publicPageBgStyle.link_style) {
        textColor = publicPageBgStyle.link_style;
      }
      this.setState({ textColor });
    }
    if (publicPageUser) {
      this.setState({ user: publicPageUser });
    }
    if (data) {
      this.setState({ product: data });
      if (data.meta_data && data.meta_data.sizes_available) {
        this.setState({ sizes: data.meta_data.sizes_available, isSizesAvailable: data.meta_data.sizes_available.length > 0 });
      }
      if (data.sizes_available) {
        this.setState({ product: data, sizes: data.sizes, isSizesAvailable: data.sizes_available });
        this.setMinMaxPrice(data.sizes);
      } else {
        this.setState({ stockAvailable: data.stock > 0, isSizesAvailable: false });
      }
    }
    if (cartItem) {
      this.setState({ cartItem });
    }
    if (cartItems) {
      this.cartItems = cartItems;
      const temp = cartItems.filter((c) => c.item.id === data.id);
      if (temp.length > 0) {
        this.setState({
          alreadyInCart: true, cartSelectedSize: temp[0].selectedSize, cartSelectedQuantity: temp[0].quantity,
        });
      } else {
        this.setState({ alreadyInCart: false, quantity: 1, selectedSize: null });
      }
    }
  }

  componentWillReceiveProps(np) {
    const { parent, publicPageUser } = this.props;
    if (parent === 'admin-page') {
      if (np.homeBgStyle) {
        let textColor = DEFAULT_TEXT_COLOR;
        if (np.homeBgStyle.link_style) {
          textColor = np.homeBgStyle.link_style;
        }
        this.setState({ textColor });
      }
    } else if (np.publicPageBgStyle) {
      let textColor = DEFAULT_TEXT_COLOR;
      if (np.publicPageBgStyle.link_style) {
        textColor = np.publicPageBgStyle.link_style;
      }
      this.setState({ textColor });
    }
    if (np.publicPageUser) {
      this.setState({ user: np.publicPageUser });
    }
    if (np.data && np.data !== this.state.product) {
      const { data } = np;
      
      this.setState({ product: { ...data,}});
      if (data.meta_data && data.meta_data.sizes_available) {
        this.setState({ sizes: data.meta_data.sizes_available, isSizesAvailable: data.meta_data.sizes_available.length > 0 });
      }
      if (np.data.sizes_available) {
        this.setState({ sizes: np.data.sizes, isSizesAvailable: np.data.sizes_available });
        this.setMinMaxPrice(np.data.sizes);
      } else {
        this.setState({ stockAvailable: np.data.stock > 0, isSizesAvailable: false });
      }
    }
    if (np.cartItem !== this.state.cartItem) {
      this.setState({ cartItem: np.cartItem });
    }
    if (np.cartItems) {
      this.cartItems = np.cartItems;
      const temp = np.cartItems.filter((c) => c.item.id === np.data.id);
      if (temp.length > 0) {
        this.setState({
          alreadyInCart: true, cartSelectedSize: temp[0].selectedSize, cartSelectedQuantity: temp[0].quantity,
        });
      } else {
        this.setState({ alreadyInCart: false, quantity: 1, selectedSize: null });
      }
    }
  }

  setMinMaxPrice(sizes) {
    if (!sizes || sizes.length <= 0) {
      this.setState({ minPrice: 0, maxPrice: 0 });
      return;
    }
    if (!sizes[0].price) return;
    const { product } = this.state;
    let minPrice = sizes[0].disc_price;
    let maxPrice = sizes[0].disc_price;
    let stockAvailable = false;
    sizes.forEach((p) => {
      minPrice = Math.min(p.disc_price, minPrice);
      maxPrice = Math.max(p.disc_price, maxPrice);
      stockAvailable = stockAvailable || p.stock > 0;
      
    });
    this.setState({ minPrice, maxPrice, stockAvailable });
  }

  showCart() {
    const { routingLocation } = this.props;
    let path = `${window.location.pathname}?`;
    const { query } = routingLocation;
    query.cartDetails = true;
    Object.keys(query).forEach((q) => {
      path += `${q}=${query[q]}&`;
    });
    path.substring(0, path.length - 2);
    store.dispatch(push(path));
  }

  modifyQuantity(change) {
    if (this.state.quantity + change > 0) this.setState({ quantity: this.state.quantity + change });
  }

  addToCart() {
    const {
      isSizesAvailable, product, quantity, selectedSize, user,
    } = this.state;
    if (isSizesAvailable && !selectedSize) {
      this.setState({ showSizes: true });
    } else {
      const data = {
        item: product,
        quantity,
        selectedSize,
        seller: user,
      };
      const sizeSelect = this.selectRef.current;
      if (sizeSelect) sizeSelect.children[0].selected = true;

      this.props.addToCart(data);
      this.setState({ showSizes: false, selectedSize: null, quantity: 1 });
      toast.info('Item added to cart !!!');
    }
  }

  removeProduct() {
    const data = {
      item: this.state.product,
      quantity: this.state.cartSelectedQuantity,
      selectedSize: this.state.cartSelectedSize,
    };

    this.props.removeFromCart(data);
  }

  selectSize(e) {
    const { sizes } = this.state;
    // console.log(sizes[e.target.value], e.target.value);
    this.setState({ selectedSize: sizes[e.target.value] });
  }

  showProductDetails() {
    const { product, user } = this.state;
    const path = `/${user.username}/products/${product.slug}`;
    store.dispatch(push(path));
  }

  render() {
    const {
      quantity, alreadyInCart, cartItem, showSizes, sizes, product, textColor, minPrice, maxPrice, isSizesAvailable, selectedSize, stockAvailable,
    } = this.state;
    const {
      name, disc_price, price, images, preview_images, cod_available, resell_margin,
    } = product;
    
    return (
      <div className="public-product relative" style={this.props.style}>

        <div className="product-header-img " style={{ overflow: 'hidden' }} onClick={this.showProductDetails}>
          {
            images && images.length > 0
              ? <Image src={images[0]} alt="" overlaySrc={preview_images[0]} /> : null
          }
        </div>
        <div className="product-pricing">
          <h3 className="ma0 " onClick={this.showProductDetails}>
            {name}
          </h3>
          {
           !isSizesAvailable ? (
             disc_price === price
               ? (
                 <b className="green">
                    Rs.
                   {price}
                 </b>
               )
               : (
                 <div>
                   <div className="home-page-product-sale">
                     {`${Math.floor(((price - disc_price) / price) * 100)}%off`}
                   </div>
                   <b>
                    Rs.
                     {disc_price}
                   </b>
                   <strike>{` Rs. ${price}`}</strike>

                 </div>
               )
           ) : (
             selectedSize ? (
               selectedSize.disc_price === selectedSize.price
                 ? (
                   <b className="green">{`Rs.${selectedSize.price}`}</b>
                 )
                 : (
                   <div>
                     <div className="home-page-product-sale">
                       {`${Math.floor(((selectedSize.price - selectedSize.disc_price) / selectedSize.price) * 100)}%off`}
                     </div>
                     <b>{`Rs.${selectedSize.disc_price}`}</b>
                     <strike>{` Rs. ${selectedSize.price}`}</strike>
                   </div>
                 )
             ) : (
               minPrice !== maxPrice
                 ? (
                   <b className="green">
                     {`Rs. ${minPrice} ~ Rs.${maxPrice}`}
                   </b>
                 )
                 : (
                   <b className="green">{`Rs.${maxPrice}`}</b>
                 )
             )
           )
          }
          {
          cartItem
            ? (
              <div className="product-bottom-div">
                <div className="f6 go-to-cart relative red" onClick={this.removeProduct.bind(this)}>
                  Remove
                </div>
                {
                  cod_available
                    ? <p className="b err-txt green">Cash on delivery is available</p>
                    : <p className="err-txt blue">Cash on delivery not available</p>
                }
              </div>
            )
            : (
              <div className="product-bottom-div">
                <div className="flex">
                  <button type="button" className="w-20" onClick={() => this.modifyQuantity(-1)}> - </button>
                  <input type="number" value={quantity} style={{ fontSize: '1.2rem' }} className="w-60 b" disabled />
                  <button type="button" className="w-20" onClick={() => this.modifyQuantity(1)}>  + </button>
                </div>

                <CollapseTransition visible={showSizes}>
                  <div className="flex flex-column mt1 mb1">
                    <select onChange={this.selectSize.bind(this)} ref={this.selectRef} id="product-size-select">
                      <option value="">Select Size</option>
                      {
                        sizes && sizes.map((s, i) => (
                          <option value={i} key={i}>{`${s.size}`}</option>
                        ))
                      }
                    </select>
                  </div>
                </CollapseTransition>

                <button
                  className="product-add-to-cart"
                  type="button"
                  style={{ backgroundColor: textColor, color: checkBgColor(textColor) }}
                  onClick={this.addToCart.bind(this)}
                  disabled={selectedSize ? selectedSize.stock <= 0 : !stockAvailable}
                >
                  {
                    selectedSize
                      ? (selectedSize.stock > 0
                        ? 'Add To Cart'
                        : 'Out of Stock'
                      )
                      : stockAvailable
                        ? 'Add To Cart'
                        : 'Out of Stock'

                  }
                </button>
                {
                  alreadyInCart
                    ? (
                      <div className=" go-to-cart relative">
Item already in cart.
                        <a onClick={this.showCart}>Go To Cart</a>
                      </div>
                    )
                    : null
                }
              </div>
            )
        }
        </div>


      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  // user: state.AuthReducer.user,
  cartItems: state.HomePageReducers.cartItems,
  routingLocation: state.router.location,
  publicPageBgStyle: state.HomePageReducers.publicPageBgStyle,
  publicPageUser: state.HomePageReducers.publicPageUser,
  homeBgStyle: state.AdminPageReducers.homeBgStyle,
});

const mapDispatchToProps = (dispatch) => ({
  addToCart: (product) => dispatch({ type: ADD_TO_CART, payload: product }),
  removeFromCart: (product) => dispatch({ type: REMOVE_FROM_CART, payload: product }),
  selectProduct: (product) => dispatch({ type: SELECT_PRODUCT, payload: product }),

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(PublicProduct);
